* {
  margin: 0;
  padding: 0;
}

.dkk-header {
  z-index: 50;
  background-color: #ffffff56;
  border-bottom: 5px solid #054b81;
  width: 100vw;
  height: 20vh;
  top: 0;
}

.dkk-header-inner {
  justify-content: space-between;
  align-items: center;
  max-width: 1120px;
  height: 100%;
  margin: 0 auto;
  padding: 0 1rem 0 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.dkk-logo img {
  background-color: #e5e7eb15;
  height: 16vh;
  display: block;
}

.dkk-nav {
  display: flex;
}

.dkk-nav-list {
  align-items: center;
  gap: 1.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.dkk-nav-item a, .dkk-nav-link-dropdown {
  color: #111827;
  align-items: center;
  gap: .25rem;
  padding: 10px;
  font-size: 19px;
  font-weight: 500;
  text-decoration: none;
  display: inline-flex;
}

.dkk-nav-item a:hover, .dkk-nav-link-dropdown:hover {
  color: #1881d2;
  background-color: #e5e7eb;
  border-radius: 30px;
  padding: 10px;
  transition: all .5s;
}

.dkk-nav-toggle {
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  gap: 5px;
  padding: .25rem;
  display: none;
}

.dkk-nav-toggle-bar {
  background-color: #111827;
  border-radius: 4px;
  width: 22px;
  height: 2px;
}

.dkk-nav-item--has-dropdown {
  position: relative;
}

.dkk-dropdown {
  z-index: 40;
  background-color: #fff;
  border-radius: .25rem;
  min-width: 180px;
  padding: .35rem 0;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -4px #0000001a;
}

.dkk-dropdown li {
  list-style: none;
}

.dkk-dropdown a {
  color: #111827;
  padding: .45rem .9rem;
  font-size: 18px;
  text-decoration: none;
  display: block;
}

.dkk-dropdown a:hover {
  background-color: #f3f4f6;
}

@media (width <= 1000px) {
  .dkk-logo img {
    height: 12vh;
  }
}

@media (width >= 768px) {
  .dkk-nav-item--has-dropdown:hover .dkk-dropdown {
    display: block;
  }
}

.dkk-nav-caret {
  font-size: 18px;
}

@media (width <= 850px) {
  .dkk-header {
    position: sticky;
    top: 0;
  }

  .dkk-header-inner {
    padding: 0 1rem;
  }

  .dkk-nav-toggle {
    display: flex;
  }

  .dkk-nav {
    inset-inline: 0;
    background-color: #fff;
    border-bottom: 1px solid #e5e7eb;
    max-height: 0;
    transition: max-height .25s;
    position: absolute;
    top: 100%;
    overflow: hidden;
  }

  .dkk-nav--open {
    background-color: #f0f8ff;
    max-height: 420px;
  }

  .dkk-nav-list {
    flex-direction: column;
    align-items: stretch;
    gap: .25rem;
    padding: .75rem 1rem 1rem;
  }

  .dkk-nav-item a, .dkk-nav-link-dropdown {
    border-radius: .25rem;
    justify-content: space-between;
    width: 100%;
    padding: .6rem .2rem;
  }

  .dkk-nav-item a:hover, .dkk-nav-link-dropdown:hover {
    background-color: #f3f4f6;
  }

  .dkk-nav-item--has-dropdown .dkk-dropdown {
    box-shadow: none;
    border-radius: 0;
    margin-top: 0;
    padding: 0;
    position: static;
  }

  .dkk-nav-item--has-dropdown .dkk-dropdown a {
    padding-left: .9rem;
  }

  .dkk-nav-item--has-dropdown .dkk-dropdown {
    display: none;
  }

  .dkk-nav-item--has-dropdown.dkk-nav-item--open .dkk-dropdown {
    display: block;
  }
}
